.friend_list {
    display: flex;
    flex-direction: column;
    list-style: none;
    font-size: 24px;
}

.item {
    display: flex;
    justify-content: center;
    align-items: center;

}

.avatar {
    margin: 20px;
    width: 100px;
}

.name {
    font-weight: bold;
    text-transform: capitalize;
}

.isOnline {
    display: block;
    padding: 10px;
    width: 10px;
    height: 10px;
    border: solid 1px ;
    border-radius: 100%;
    background: green;
}

.isOffline {
    display: block;
    padding: 10px;
    width: 10px;
    height: 10px;
    border: solid 1px ;
    border-radius: 100%;
    background: red;
}