.statistics {
    display: block;
    margin: auto;
    width: 33%;
    font-size: 24px;
   
}

.title {
    display: flex;
    justify-content: center;

    font-size: 30px;
    text-transform: uppercase;
    color: gray;
}

.stat_list {
    display: flex;
    
    margin: 0px;
    padding: 0px;
    list-style: none;

}

.item {
    display:flex ;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px;
    width: 100%;
    border: solid 1px greenyellow;

}

.label {
    font-size: 16px;
}